@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Roboto:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cookie&display=swap');

body, html, #root {
  height: 100%;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.App {
  height: 100%;
  width: 100%;
  background: 
    url('./assets/images/page_background.jpg');
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Center card */
.overlay {
  background:
    linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2)), 
    url('./assets/images/overlay_background.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 40px 5vw 2vw 5vw;
  border-radius: 25px;
  box-shadow: 0 15px 30px rgba(0,0,0,0.15);
  text-align: center;
  min-width: 300px;
}

/* Title */
h1 {
  font-family: 'Cookie', cursive;
  font-weight: 900;
  font-size: 5em;
  color: #ff69b4;
  -webkit-text-stroke: 0.5px #ffffff;
}

/* SubTitles*/
h4 {
  font-family: 'Cookie', cursive;
  font-size: 2em;
  margin: 0%;
  color: #ff69b4;
  -webkit-text-stroke: 0.5px #ffffff;
}

/* Countdown row */
.countdown {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 20px;
}

/* Colon */
.colon {
  font-size: 2em;
  color: #333;
  margin-top: 4%;
}

/* Number */
.number {
  font-family: 'Cookie', cursive;
}

/* Calendar container */
.calendar-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Flip container */
.flip-card {
  position: relative;
  width: 110px;
  height: 70px;
  perspective: 1200px;
}

/* Shared styles for all halves */
.top, .bottom, .flip-top, .flip-bottom {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "Courier New", monospace;
  font-size: 2.2em;
  font-weight: bold;
  color: #333;

  background: linear-gradient(180deg, #ffffff 0%, #f6f6f6 100%);
  border-radius: 14px;
  padding: 3px 0 0 0;

  backface-visibility: hidden;
}

/* Static top half */
.top {
  top: 0;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
}

/* Static bottom half */
.bottom {
  bottom: 0;
  transform: rotateX(180deg);
  border-top: 1px solid #ddd;
}

/* Animated top half */
.flip-top {
  top: 0;
  transform-origin: bottom;
  animation: flipTop 0.6s ease-in forwards;
}

/* Animated bottom half */
.flip-bottom {
  bottom: 0;
  transform-origin: top;
  animation: flipBottom 0.6s ease-out forwards;
  
}

/* Top half animation */
@keyframes flipTop {
  0%   { transform: rotateX(0deg); }
  100% { transform: rotateX(-90deg); }
}

/* Bottom half animation */
@keyframes flipBottom {
  0%   { transform: rotateX(90deg); }
  100% { transform: rotateX(0deg); }
}

/* Label */
.label {
  font-size: 1.5em;
  margin-top: 6px;
  font-family: 'Playfair Display', serif;
  font-weight: 500;
  color: #d63384;
}

/* Container for each card */
.calendar-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}


/******************************************************/
/* ============================================
   WEDDING WEBSITE STYLES
   Palette: Rose, Stone, White
   ============================================ */

/* CSS Variables */
:root {
  --color-rose-400: #fb7185;
  --color-rose-300: #fda4af;
  --color-rose-200: #fecdd3;
  --color-rose-100: #ffe4e6;
  --color-rose-50: #fff1f2;
  
  --color-stone-800: #292524;
  --color-stone-700: #44403c;
  --color-stone-600: #57534e;
  --color-stone-500: #78716c;
  --color-stone-400: #a8a29e;
  --color-stone-300: #d6d3d1;
  --color-stone-200: #e7e5e4;
  --color-stone-100: #f5f5f4;
  --color-stone-50: #fafaf9;
  
  --font-serif: 'Georgia', 'Times New Roman', serif;
  --transition-base: all 0.3s ease;
  --shadow-card: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

/* Reset & Base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wedding-container {
  min-height: 100vh;
  background-color: var(--color-stone-50);
  color: var(--color-stone-800);
  font-family: var(--font-serif);
  overflow-x: hidden;
}

/* Utility Classes */
.container {
  max-width: 72rem;
  margin: 0 auto;
  padding: 0 1rem;
}

.text-center {
  text-align: center;
}

/* ============================================
   HERO SECTION
   ============================================ */
.hero-section {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.hero-bg {
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom, rgba(255, 228, 230, 0.3), rgba(245, 245, 244, 0.5), var(--color-stone-50));
  z-index: 0;
}

.hero-content {
  position: relative;
  z-index: 10;
  text-align: center;
  padding: 0 1rem;
  max-width: 56rem;
  margin: 0 auto;
}

.hero-text {
  margin-bottom: 2rem;
}

.hero-icon {
  width: 3rem;
  height: 3rem;
  color: var(--color-rose-400);
  margin: 0 auto 1.5rem;
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.hero-subtitle {
  font-size: 1.125rem;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: var(--color-stone-500);
  margin-bottom: 1rem;
}

.hero-title {
  font-size: 3rem;
  font-weight: 300;
  color: var(--color-stone-800);
  margin-bottom: 0.5rem;
  letter-spacing: -0.025em;
  line-height: 1.1;
}

.hero-title-accent {
  color: var(--color-rose-400);
  font-weight: 400;
}

.hero-details {
  font-size: 1.25rem;
  color: var(--color-stone-600);
  font-weight: 300;
  letter-spacing: 0.05em;
}

.hero-location {
  font-size: 1rem;
  color: var(--color-stone-400);
  margin-top: 0.5rem;
}

.scroll-btn {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  background: none;
  border: none;
  cursor: pointer;
  animation: bounce 1s infinite;
  transition: var(--transition-base);
}

.scroll-btn:hover {
  color: var(--color-rose-400);
}

.scroll-icon {
  width: 2rem;
  height: 2rem;
  color: var(--color-stone-400);
  transition: var(--transition-base);
}

.scroll-btn:hover .scroll-icon {
  color: var(--color-rose-400);
}

/* Decorative Blobs */
.decorative-blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(3rem);
  opacity: 0.2;
}

.blob-1 {
  width: 8rem;
  height: 8rem;
  background-color: var(--color-rose-300);
  top: 5rem;
  left: 2.5rem;
  animation: float 6s ease-in-out infinite;
}

.blob-2 {
  width: 12rem;
  height: 12rem;
  background-color: #fed7aa;
  bottom: 5rem;
  right: 2.5rem;
  animation: float 6s ease-in-out infinite;
  animation-delay: 3s;
}

/* ============================================
   COUNTDOWN SECTION
   ============================================ */
.countdown-section {
  padding: 5rem 0;
  background-color: white;
}

.section-title {
  font-size: 1.875rem;
  font-weight: 300;
  margin-bottom: 3rem;
  color: var(--color-stone-800);
}

.section-title.light {
  color: var(--color-stone-100);
}

.countdown-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  max-width: 42rem;
  margin: 0 auto;
}

.countdown-card {
  text-align: center;
  padding: 1.5rem;
  background-color: var(--color-stone-50);
  border-radius: 1rem;
  border: 1px solid var(--color-stone-100);
  box-shadow: var(--shadow-sm);
  transition: var(--transition-base);
}

.countdown-card:hover {
  box-shadow: var(--shadow-card);
}

.countdown-value {
  font-size: 2.25rem;
  font-weight: 300;
  color: var(--color-rose-400);
  margin-bottom: 0.5rem;
  line-height: 1;
}

.countdown-label {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--color-stone-500);
}

/* ============================================
   DETAILS SECTION (Ceremony & Reception)
   ============================================ */
.details-section {
  padding: 6rem 0;
  background-color: var(--color-stone-50);
}

.details-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  align-items: center;
}

.details-card {
  background-color: white;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: var(--shadow-card);
  transition: all 1s ease;
}

.card-icon {
  width: 2.5rem;
  height: 2.5rem;
  color: var(--color-rose-400);
  margin-bottom: 1.5rem;
}

.card-title {
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 1rem;
  color: var(--color-stone-800);
}

.card-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.info-row {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: var(--color-stone-600);
}

.info-icon {
  width: 1.25rem;
  height: 1.25rem;
  color: var(--color-rose-300);
}

.card-description {
  font-size: 0.875rem;
  color: var(--color-stone-400);
  margin-top: 1rem;
  line-height: 1.625;
}

/* Animation States */
.animate-on-scroll {
  transition: all 1s ease;
}

.hidden {
  opacity: 0;
}

.visible {
  opacity: 1;
}

.slide-left {
  transform: translateX(3rem);
}

.slide-right {
  transform: translateX(-3rem);
}

.visible.slide-left,
.visible.slide-right {
  transform: translateX(0);
}

/* ============================================
   TIMELINE SECTION
   ============================================ */
.timeline-section {
  padding: 6rem 0;
  background-color: white;
}

.timeline {
  position: relative;
  max-width: 56rem;
  margin: 0 auto;
}

.timeline-line {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: 1px;
  background-color: var(--color-rose-200);
  display: none;
}

.timeline-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-bottom: 3rem;
  transition: all 0.7s ease;
}

.timeline-item.hidden {
  opacity: 0;
  transform: translateY(2rem);
}

.timeline-item.visible {
  opacity: 1;
  transform: translateY(0);
}

.timeline-content {
  flex: 1;
  text-align: center;
}

.timeline-node {
  position: relative;
  z-index: 10;
  width: 4rem;
  height: 4rem;
  background-color: var(--color-rose-50);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 4px solid white;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
}

.timeline-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: var(--color-rose-400);
}

.timeline-time {
  position: absolute;
  top: -2rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--color-rose-400);
  white-space: nowrap;
}

.timeline-title {
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--color-stone-800);
  margin-bottom: 0.25rem;
}

.timeline-desc {
  color: var(--color-stone-500);
}

/* ============================================
   REGISTRY SECTION
   ============================================ */
.registry-section {
  padding: 6rem 0;
  background-color: var(--color-stone-50);
}

.section-icon {
  width: 3rem;
  height: 3rem;
  color: var(--color-rose-400);
  margin: 0 auto 1.5rem;
}

.registry-text {
  color: var(--color-stone-600);
  line-height: 1.75;
  margin-bottom: 2rem;
  font-size: 1.125rem;
  max-width: 36rem;
  margin-left: auto;
  margin-right: auto;
}

/* Buttons */
.btn {
  padding: 1rem 2rem;
  border-radius: 9999px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition-base);
  font-family: var(--font-serif);
  letter-spacing: 0.05em;
}

.btn-primary {
  background-color: var(--color-stone-800);
  color: white;
}

.btn-primary:hover {
  background-color: var(--color-rose-400);
}

.btn-accent {
  background-color: var(--color-rose-400);
  color: white;
  font-weight: 500;
}

.btn-accent:hover {
  background-color: #f43f5e;
}

.btn-full {
  width: 100%;
  border-radius: 0.5rem;
}

/* ============================================
   DRESS CODE SECTION
   ============================================ */
.dresscode-section {
  padding: 6rem 0;
  background-color: white;
}

.dresscode-card {
  background: linear-gradient(to bottom right, var(--color-stone-100), var(--color-rose-50));
  border-radius: 1.5rem;
  padding: 2rem;
  text-align: center;
}

.dresscode-subtitle {
  font-size: 1.25rem;
  color: var(--color-stone-600);
  margin-bottom: 1rem;
}

.color-palette {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.color-dot {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.2s;
}

.color-dot:hover {
  transform: scale(1.1);
}

.color-dark { background-color: var(--color-stone-700); }
.color-rose { background-color: var(--color-rose-300); }
.color-gray { background-color: var(--color-stone-300); }
.color-champagne { background-color: #ffedd5; }

.dresscode-desc {
  color: var(--color-stone-500);
  max-width: 32rem;
  margin: 0 auto;
  line-height: 1.625;
}

/* ============================================
   RSVP SECTION
   ============================================ */
.rsvp-section {
  padding: 6rem 0;
  background-color: var(--color-stone-800);
  color: var(--color-stone-100);
}

.rsvp-subtitle {
  color: var(--color-stone-400);
  margin-bottom: 3rem;
  font-size: 1.125rem;
}

.rsvp-form {
  max-width: 32rem;
  margin: 0 auto;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--color-stone-400);
}

.form-input {
  width: 100%;
  background-color: rgba(68, 64, 60, 0.5);
  border: 1px solid var(--color-stone-600);
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  color: var(--color-stone-100);
  font-family: var(--font-serif);
  font-size: 1rem;
  transition: var(--transition-base);
}

.form-input:focus {
  outline: none;
  border-color: var(--color-rose-400);
}

.form-input::placeholder {
  color: var(--color-stone-500);
}

.form-select {
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%23a8a29e' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 0.5rem center;
  background-repeat: no-repeat;
  background-size: 1.5em 1.5em;
  padding-right: 2.5rem;
}

.form-textarea {
  min-height: 6rem;
  resize: vertical;
}

/* ============================================
   CONTACT SECTION
   ============================================ */
.contact-section {
  padding: 5rem 0;
  background-color: var(--color-stone-100);
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
}

.contact-block {
  text-align: center;
}

.contact-block.text-right {
  text-align: center;
}

.contact-title {
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 1.5rem;
  color: var(--color-stone-800);
}

.contact-links {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  color: var(--color-stone-600);
  text-decoration: none;
  transition: var(--transition-base);
}

.contact-link:hover {
  color: var(--color-rose-400);
}

.contact-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.contact-text {
  color: var(--color-stone-600);
  line-height: 1.625;
}

.highlight-code {
  font-weight: 500;
  color: var(--color-rose-400);
}

/* ============================================
   FOOTER
   ============================================ */
.footer {
  padding: 3rem 0;
  background-color: var(--color-stone-50);
  text-align: center;
  border-top: 1px solid var(--color-stone-200);
}

.footer-icon {
  width: 2rem;
  height: 2rem;
  color: var(--color-rose-400);
  margin: 0 auto 1rem;
}

.footer-text {
  color: var(--color-stone-500);
  font-weight: 300;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  font-size: 0.875rem;
}

/* ============================================
   ANIMATIONS
   ============================================ */
@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

@keyframes bounce {
  0%, 100% { transform: translateX(-50%) translateY(0); }
  50% { transform: translateX(-50%) translateY(-10px); }
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

@keyframes fade-in {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.animate-fade-in {
  animation: fade-in 1.5s ease-out;
}

/* ============================================
   RESPONSIVE DESIGN
   ============================================ */
@media (min-width: 768px) {
  .hero-title {
    font-size: 4.5rem;
  }
  
  .details-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .details-card {
    padding: 3rem;
  }
  
  .timeline-line {
    display: block;
  }
  
  .timeline-item {
    flex-direction: row;
  }
  
  .timeline-item.reverse {
    flex-direction: row-reverse;
  }
  
  .timeline-content {
    text-align: right;
  }
  
  .timeline-item.reverse .timeline-content {
    text-align: left;
  }
  
  .form-row {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .contact-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .contact-block.text-right {
    text-align: right;
  }
  
  .contact-link {
    justify-content: flex-start;
  }
  
  .contact-block.text-right .contact-link {
    justify-content: flex-end;
  }
  
  .dresscode-card {
    padding: 4rem;
  }
  
  .section-title {
    font-size: 2.25rem;
  }
  
  .countdown-value {
    font-size: 3rem;
  }
}

@media (min-width: 1024px) {
  .hero-title {
    font-size: 6rem;
  }
  
  .container {
    padding: 0 2rem;
  }
}
/******************************************************/

/* Responsive */
/* Small smartphones: up to 400px */
@media (max-width: 400px) {
  .flip-card {
    width: 50px;
    height: 45px;
  }

  .top, .bottom, .flip-top, .flip-bottom {
    font-size: 1.2em;
    padding: 2px 0 0 0;
  }

  .label {
    font-size: 1.2em;
  }

  .colon{
    margin-top: 2%;
  }

  h1 {
    font-size: 3em;
    -webkit-text-stroke: 0.2px #ffffff;
  }

  h4 {
    font-size: 1.5em;
    -webkit-text-stroke: 0.1px #ffffff;
  }
}

/* Smartphones: 401px – 600px */
@media (min-width: 401px) and (max-width: 600px) {
  .flip-card {
    width: 60px;
    height: 55px;
  }

  .top, .bottom, .flip-top, .flip-bottom {
    font-size: 1.6em;
  }

  .label {
    font-size: 1.3em;
  }

  h1 {
    font-size: 4em;
    -webkit-text-stroke: 0.3px #ffffff;
  }

  h4 {
    font-size: 1.7em;
    -webkit-text-stroke: 0.1px #ffffff;
  }
}

/* Tablets: 601px – 900px */
@media (min-width: 601px) and (max-width: 900px) {
  .flip-card {
    width: 90px;
    height: 60px;
  }

  .top, .bottom, .flip-top, .flip-bottom {
    font-size: 2em;
  }

  .label {
    font-size: 1.4em;
  }

  h1 {
    font-size: 4.5em;
  }

  h4 {
    font-size: 1.8em;
  }
}

/* Small laptops: 901px – 1200px */
@media (min-width: 901px) and (max-width: 1200px) {
  .flip-card {
    width: 100px;
    height: 65px;
  }

  .top, .bottom, .flip-top, .flip-bottom {
    font-size: 2.1em;
  }

  .label {
    font-size: 1.5em;
  }

  h1 {
    font-size: 5em;
  }

  h4 {
    font-size: 2em;
  }
}

/* Large laptops / desktops: 1201px – 1600px */
@media (min-width: 1201px) and (max-width: 1600px) {
  .flip-card {
    width: 110px;
    height: 70px;
  }

  .top, .bottom, .flip-top, .flip-bottom {
