    font-size: 5.5em;
  }

  h4 {
    font-size: 2em;
  }
}

/* Extra large screens: 1601px and above */
@media (min-width: 1601px) {
  .flip-card {
    width: 120px;
    height: 80px;
  }

  .top, .bottom, .flip-top, .flip-bottom {
    font-size: 2.5em;
  }

